import axios from 'axios';
import { disableBodyScroll, enableBodyScroll } from 'body-scroll-lock';

let switcher = false;
let hotels = [];
let stars = '';
let price = '';



// Add pirates
document.querySelector('.visitors__number').innerHTML = Math.round(Math.random() * 20);

// Create stars
function createStars(stars) {
  let starsString = '';
  for (let index = 0; index < stars; index++) {
    starsString += '<svg width="18" height="18" viewBox="0 0 24 24" fill="#CFA55B"><path d="M0 0h24v24H0z" fill="none"/><path d="M12 17.27L18.18 21l-1.64-7.03L22 9.24l-7.19-.61L12 2 9.19 8.63 2 9.24l5.46 4.73L5.82 21z"/><path d="M0 0h24v24H0z" fill="none"/></svg>';
  }
  return starsString;
}

// Estimate rating
function estimateRating(rating) {
  let exactRating = Math.floor(rating);
  if (exactRating == 0) {
    return 'None';
  } else if (exactRating == 1) {
    return 'Poor';
  } else if (exactRating == 2) {
    return 'Unsatisfactory';
  } else if (exactRating == 3) {
    return 'Satisfactory';
  } else if (exactRating == 4) {
    return 'Good';
  } else if (exactRating == 5) {
    return 'Excellent';
  }
}

// Create hotel list
function hotelList(hotels) {
  return hotels.map(hotel => {
    let element = document.createElement('div');
    element.classList.add('hotel');
    element.innerHTML = `<div class="hotel__imgbox">
                            <svg class="hotel__icon" width="80" height="80" viewBox="0 0 24 24" fill="#CFA55B"><circle cx="12" cy="12" r="3.2"/><path d="M9 2L7.17 4H4c-1.1 0-2 .9-2 2v12c0 1.1.9 2 2 2h16c1.1 0 2-.9 2-2V6c0-1.1-.9-2-2-2h-3.17L15 2H9zm3 15c-2.76 0-5-2.24-5-5s2.24-5 5-5 5 2.24 5 5-2.24 5-5 5z"/><path d="M0 0h24v24H0z" fill="none"/></svg>
                            <img class="hotel__img" src="${hotel.images[1]}">
                            <div class="hotel__cover"></div>
                            <div class="hotel__stars">${createStars(hotel.stars)}</div>
                          </div>
                          <div class="hotel__content">
                            <div>
                              <div class="hotel__title">${hotel.name}</div>
                              <div class="hotel__description">${hotel.city}, ${hotel.country}</div>
                            </div>
                            <div>
                              <div class="hotel__flex">
                                <div class="hotel__date">From: ${new Date(hotel.date_start).toLocaleDateString()}</div>
                                <div class="hotel__date">To: ${new Date(hotel.date_end).toLocaleDateString()}</div>
                              </div>
                              <div class="hotel__flex">
                                <div class="hotel__price">EUR ${hotel.price}</div>
                              </div>
                              <div class="hotel__flex">
                                <div class="hotel__rating">
                                  <button class="hotel__ratingbox">${Math.floor(hotel.rating)}</button>
                                  <div class="hotel__text">${estimateRating(hotel.rating)}</div>
                                </div>
                                <button class="hotel__button" data-id="${hotel.id}">Reviews</button>
                              </div>
                            </div>
                            <a class="hotel__link" href=""></a>
                          </div>`;
    return element;
  });
}

// Create reviews list
function reviewList(reviews) {
  return reviews.map(review => {
    let element = document.createElement('div');
    element.classList.add('review');
    element.innerHTML = `<div class="">
                            <div class="review__header">
                              <div class="review__name">${review.name}</div>
                              ${review.positive ? '<svg width="24" height="24" viewBox="0 0 24 24" fill="#CFA55B"><path fill="none" d="M0 0h24v24H0V0z"/><path d="M1 21h4V9H1v12zm22-11c0-1.1-.9-2-2-2h-6.31l.95-4.57.03-.32c0-.41-.17-.79-.44-1.06L14.17 1 7.59 7.59C7.22 7.95 7 8.45 7 9v10c0 1.1.9 2 2 2h9c.83 0 1.54-.5 1.84-1.22l3.02-7.05c.09-.23.14-.47.14-.73v-2z"/></svg>' : '<svg width="24" height="24" viewBox="0 0 24 24" fill="#FF6B6B"><path fill="none" d="M0 0h24v24H0z"/><path d="M15 3H6c-.83 0-1.54.5-1.84 1.22l-3.02 7.05c-.09.23-.14.47-.14.73v2c0 1.1.9 2 2 2h6.31l-.95 4.57-.03.32c0 .41.17.79.44 1.06L9.83 23l6.59-6.59c.36-.36.58-.86.58-1.41V5c0-1.1-.9-2-2-2zm4 0v12h4V3h-4z"/></svg>'}
                            </div>
                            <div class="review__comment">${review.comment}</div>
                          </div>`;
    return element;
  });
}

// Update hotels
function updateHotels(hotels) {
  let currentHotels = document.querySelectorAll('.hotel');

  currentHotels.forEach(currentHotel => currentHotel.remove());
  hotelList(hotels).forEach(hotel => {
    document.querySelector('.grid').appendChild(hotel);
    hotel.querySelector('img').addEventListener('load', function() {
      this.nextElementSibling.classList.add('hotel__cover--active');
      this.classList.add('hotel__img--active')
    });
    hotel.querySelector('img').addEventListener('error', function() { this.remove() });
  });
}

// Add hotels
function addHotels(hotels) {
  hotelList(hotels).forEach(hotel => {
    document.querySelector('.grid').appendChild(hotel)
    hotel.querySelector('img').addEventListener('load', function() {
      this.nextElementSibling.classList.add('hotel__cover--active');
      this.classList.add('hotel__img--active')
    });
    hotel.querySelector('img').addEventListener('error', function() { this.remove() });
  });
  switcher = true;
}

// Add reviews
function addReviews(reviews) {
  reviewList(reviews).forEach(review => {
    document.querySelector('.modal__content').appendChild(review);
  });
}

//Default request
function getHotels() {
  axios.get('http://fake-hotel-api.herokuapp.com/api/hotels', {
      params: {
        no_error: true,
        min_stars: 3,
        count: 30,
      }
    })
    .then(response => response.data)
    .then((items) => {
      items.forEach(item => hotels.push(item));
      let filteredItems = items;

      if (stars != '') {
        filteredItems = filteredItems.filter(item => item.stars == stars);
      }
      if (price == 1) {
        filteredItems = filteredItems.filter(item => item.price < 300);
      }
      if (price == 2) {
        filteredItems = filteredItems.filter(item => item.price > 300 && item.price < 700);
      }
      if (price == 3) {
        filteredItems = filteredItems.filter(item => item.price > 700);
      }

      addHotels(filteredItems);

    })
    .catch(error => console.log(error));
}
getHotels();

// Load on scroll
function loadOnScroll() {
  let windowTopPosition = window.pageYOffset + window.innerHeight;
  let hotelListenerPosition = document.querySelector('.grid').offsetTop + document.querySelector('.grid').offsetHeight - 500;
  if (switcher == true && windowTopPosition > hotelListenerPosition) {
    switcher = false;
    getHotels();
  }
}
document.addEventListener('scroll', loadOnScroll);

// Stars input
function filterByStars() {
  stars = document.querySelector('#starSelect').value;
  let filteredItems = hotels;

  if (stars != '') {
    filteredItems = filteredItems.filter(item => item.stars == stars);

    if (price != '') {
      if (price == 1) {
        filteredItems = filteredItems.filter(item => item.price < 300);
      }
      if (price == 2) {
        filteredItems = filteredItems.filter(item => item.price > 300 && item.price < 700);
      }
      if (price == 3) {
        filteredItems = filteredItems.filter(item => item.price > 700);
      }
    }

    updateHotels(filteredItems);
  }
}
document.querySelector('#starSelect').addEventListener('change', filterByStars);

// Price input
function filterByPrice() {
  price = document.querySelector('#priceSelect').value;
  let filteredItems = hotels;

  if (price != '') {
    if (stars != '') {
      filteredItems = filteredItems.filter(item => item.stars == stars);
    }
    if (price == 1) {
      filteredItems = filteredItems.filter(item => item.price < 300);
    }
    if (price == 2) {
      filteredItems = filteredItems.filter(item => item.price > 300 && item.price < 700);
    }
    if (price == 3) {
      filteredItems = filteredItems.filter(item => item.price > 700);
    }

    updateHotels(filteredItems);
  }
}
document.querySelector('#priceSelect').addEventListener('change', filterByPrice);

// Get reviews
function getReviews(e) {
  if (e.target.matches('.hotel__button')) {
    let modal = document.querySelector('.modal');

    modal.classList.toggle('modal--active');
    disableBodyScroll(modal);

    axios.get('http://fake-hotel-api.herokuapp.com/api/reviews', {
        params: {
          hotel_id: e.target.dataset.id,
        }
      })
      .then(response => response.data)
      .then((items) => {
        document.querySelector('.lds-ring').classList.add('lds-ring--hidden');

        if (items.length > 0) {
          addReviews(items);
        } else {
          let element = document.createElement('div');
          element.classList.add('review');
          element.textContent = 'There are no reviews for this hotel, please come back later';
          document.querySelector('.modal__content').appendChild(element);
        }



      })
      .catch(error => console.log(error));
  }
}
document.addEventListener('click', getReviews);

// Close reviews
function closeReviews() {
  let modal = document.querySelector('.modal');

  modal.classList.toggle('modal--active');
  enableBodyScroll(modal);
  document.querySelectorAll('.review').forEach(review => review.remove());
  document.querySelector('.lds-ring').classList.remove('lds-ring--hidden');
}
document.querySelector('.modal__close').addEventListener('click', closeReviews);